

.iconos-nosotros{
    @include grid(3,2rem);
}

@include tablet{
    .iconos-nosotros{
        display: block;
    }
 
}

.icono{

    text-align: center;
    img{
        height: 10rem;
    }
    h3{
        text-transform: uppercase;
    }
    p{
        text-align: justify;
    }
}