  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none; }
  .boton-rojo:hover {
    background-color: #d80505;
    cursor: pointer; }

.boton-amarillo {
  background-color: #E08709;
  font-weight: 700;
  color: #FFFFFF;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none; }
  .boton-amarillo:hover {
    background-color: #c77808;
    cursor: pointer; }

/*UI*/
.dark-mode {
  background-color: #1a1a1a; }
  .dark-mode p {
    color: #FFFFFF; }
  .dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
    color: #e1e1e1; }
  .dark-mode .anuncio {
    background-color: #333333;
    border: 1px solid #e1e1e1; }
    .dark-mode .anuncio .precio {
      color: #71B100; }
    .dark-mode .anuncio .icono {
      filter: invert(100%); }
  .dark-mode .formulario label {
    color: white; }

.no-webp .inicio {
  background-image: url(../img/header.jpg); }

.webp .inicio {
  background-image: url(../img/header.webp); }

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0; }
  .header.inicio {
    min-height: 50rem;
    background-position: center center;
    background-size: cover;
    min-height: 50rem; }
    .header.inicio .contenido-header {
      min-height: 50rem; }

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .contenido-header h1 {
    text-align: left;
    color: #FFFFFF;
    font-weight: 700;
    max-width: 60rem; }

.footer {
  background-color: #333333;
  padding: 1.25rem 0;
  margin-bottom: 0; }
  .footer .contenedor-footer {
    padding-top: 2.5rem; }
    .footer .contenedor-footer .nav-header {
      display: flex;
      justify-content: space-evenly; }

.copyright {
  color: #FFFFFF;
  text-align: center; }

@media (max-width: 768px) {
  .contenedor-footer .nav-header {
    display: flex;
    flex-direction: column;
    justify-content: center; }
    .contenedor-footer .nav-header a {
      visibility: visible; } }

.barra {
  display: flex;
  justify-content: space-between;
  padding-top: 3rem;
  align-items: center; }
  @media (max-width: 768px) {
    .barra {
      flex-direction: column; } }

.nav-header a {
  color: #FFFFFF;
  text-decoration: none;
  margin-right: 1rem;
  font-size: 1.8rem;
  align-items: center; }
  .nav-header a:last-of-type {
    margin-right: 0; }
  @media (max-width: 768px) {
    .nav-header a {
      visibility: hidden;
      text-align: center; } }

/*Menu hamburguesa*/
.menu-mobile {
  display: none; }

@media (max-width: 768px) {
  .menu-mobile {
    display: block; }
    .menu-mobile img {
      width: 4rem; } }

.mostrar a {
  visibility: visible; }

@media (max-width: 768px) {
  .mostrar a {
    display: block; } }

.dark-mode-boton {
  margin-bottom: 1rem;
  width: 2rem;
  filter: invert(100%); }
  .dark-mode-boton:hover {
    cursor: pointer; }

.derecha {
  display: flex;
  flex-direction: column;
  align-items: flex-end; }

@media (max-width: 768px) {
  .derecha {
    align-items: center;
    margin-top: 3rem; } }

.iconos-nosotros {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr); }

@media (max-width: 768px) {
  .iconos-nosotros {
    display: block; } }

.icono {
  text-align: center; }
  .icono img {
    height: 10rem; }
  .icono h3 {
    text-transform: uppercase; }
  .icono p {
    text-align: justify; }

.contenedor-anuncio {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr); }

@media (max-width: 768px) {
  .contenedor-anuncio {
    display: grid;
    grid-template-columns: repeat(2, 1fr); } }

@media (max-width: 480px) {
  .contenedor-anuncio {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(1, 1fr); } }

.anuncio {
  background-color: #eeeeee;
  border: 2px solid #e1e1e1; }
  .anuncio .contenido-anuncio {
    padding: 2rem; }
  .anuncio h3, .anuncio p {
    margin: 0; }

.precio {
  color: #71B100; }

.icono-anuncio {
  display: flex;
  text-decoration: none;
  list-style: none;
  padding: 0; }
  .icono-anuncio li {
    flex: 1;
    display: flex; }
  .icono-anuncio img {
    flex: 0 0 4rem;
    margin-right: 1rem; }

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg); }

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp); }

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 0; }
  .imagen-contacto h3 {
    color: #FFFFFF;
    font-size: 3.5rem;
    font-weight: 900; }
  .imagen-contacto p {
    color: #FFFFFF;
    font-size: 1.8rem; }

.seccion-blog {
  display: grid;
  grid-template-columns: 2fr 1fr;
  column-gap: 2rem; }

@media (max-width: 768px) {
  .seccion-blog {
    display: block; } }

.entrada-blog {
  margin-bottom: 2rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  column-gap: 2rem; }
  .entrada-blog:last-of-type {
    margin: 0; }

@media (max-width: 768px) {
  .entrada-blog {
    display: block; } }

.quotes span {
  color: #E08709; }

.texto-entrada a {
  color: #000000; }

.texto-entrada h4 {
  margin: 0;
  line-height: 1.4; }
  .texto-entrada h4::after {
    content: '';
    display: block;
    background-color: #71B100;
    height: 5px;
    width: 15rem; }

.testimoniales {
  background-color: #71B100;
  padding: 2rem;
  color: #FFFFFF;
  font-size: 2.4rem;
  border-radius: 2rem; }
  .testimoniales blockquote {
    padding-left: 5rem;
    position: relative; }
    .testimoniales blockquote::before {
      content: '';
      background-image: url(../img/comilla.svg);
      height: 8rem;
      width: 6rem;
      background-repeat: no-repeat;
      display: block;
      position: absolute;
      left: -2rem; }
  .testimoniales p {
    color: #FFFFFF;
    text-align: right; }

.formulario p {
  font-size: 1.5rem;
  color: #333333;
  margin: 2rem 0 0 0; }

.formulario legend {
  font-size: 2rem;
  color: #333333; }

.formulario label {
  text-transform: uppercase;
  font-weight: 700;
  display: block; }

.formulario input:not([type="submit"]),
.formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem; }

.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.formulario input[type=radio] {
  margin: 0;
  width: auto; }

/*Secciones*/
.seccion-nosotros {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  text-align: justify; }

@media (max-width: 768px) {
  .seccion-nosotros {
    display: block; } }

blockquote {
  font-size: 2.2rem;
  font-weight: 900;
  margin: 0; }

.alerta {
  color: #FFFFFF;
  text-transform: uppercase;
  padding: .5rem;
  margin: 1rem 0;
  font-weight: 700;
  text-align: center; }
  .alerta.error {
    background-color: red; }
  .alerta.exito {
    background-color: #71B100 !important; }

table.propiedades {
  margin: 10rem 0;
  width: 100%;
  border-spacing: 0; }
  table.propiedades thead {
    background-color: #71B100; }
    table.propiedades thead th {
