.formulario{
    p{
        font-size: 1.5rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0 ;
    }
    legend{
        font-size: 2rem;
        color: $grisOscuro;
    }
    label{
        text-transform: uppercase;
        font-weight: $bold;
        display: block;
 
    }
    input:not([type="submit"]),
    textarea,select{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;

}

 .forma-contacto{
     max-width: 30rem;
     display: flex;
     justify-content: space-between;
     align-items: center;
 }

 input[type=radio]{
     margin: 0;
     width: auto;

 }

}